export const SET_IS_OPEN_CON='[funnel] SET_IS_OPEN_CON'
export const SET_THUMBTACK ='[funnel] SET_THUMBTACK'

export function setFlagToggleCon(newFlag){
    return{
        type:SET_IS_OPEN_CON,
        payload:newFlag
    }
}
export function setFlagthumbtack(newFlag){
    return{
        type:SET_THUMBTACK,
        payload:newFlag
    }
}